@import "../../commonStylles/commonStyles.scss";

.search-form {
  margin: 0;
  padding: 24px 0 70px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 42px;
  border-bottom: 1px solid #424242;
  position: relative;
}
.search-form__field-container {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  align-items: center;
}
.search-form__field {
  width: 100%;
  margin: 0;
  padding: 14px 22px;
  background-color: $text-input-bkg;
  height: 46px;
  border-radius: 23px;
  border: none;
  font-size: 13px;
  line-height: 17px;
  box-sizing: border-box;
  color: #fff;
  outline: none;
  &:focus {
    outline: 1px solid #8b8b8b;
  }
  @media (max-width: 500px) {
    margin-left: 4px;
    margin-right: 4px;
    padding: 14px 19px;
    font-size: 11px;
  }
}
.search-form__submit-button {
  margin: 0;
  margin-left: -86px;
  padding: 0;
  height: 34px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 17px;
  background-color: $blue-solid;
  border: none;
  @media (max-width: 500px) {
    margin-left: -90px;
  }
}

.search-form__validation-error {
  display: none;
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 10px;
  letter-spacing: 0em;
  color: #ee3465;
  position: absolute;
  left: 30px;
  bottom: 110px;
}
.search-form__validation-error_state_shown {
  display: block;
}
